.social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  @include listReset();

  margin-bottom: 1.2rem;

  &__item:not(:last-child) {
    margin-right: 0.8rem;

    @media (min-width: $sm) {
      margin-right: 13px;
    }
  }

  @media (min-width: $sm) {
    margin-bottom: 0;
  }

  &__link {
    display: block;
    color: $white-color;

    &:focus,
    &:hover {
      color: $yellow-color;
    }

    &--vk .icon {
      width: 27px;
      height: 16px;
    }
    &--tm .icon {
      width: 24px;
      height: 24px;
    }
    &--youtube .icon {
      width: 25px;
      height: 18px;
    }

  }

  & .icon {
    fill: $white-color;
  }
}
