.map {
  padding-top: 54px;
  background-color: $bg-color;

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
    max-width: 1440px;
    margin-top: 0;
    margin: 0 auto;

    // background-image: url('../img/clubs/map.jpg');
    background-size: cover;
    min-height: 610px;
    overflow: hidden;

    margin-top: 100px;

    @media (min-width: $sm) {
      margin-top: 124px;
    }
    @media (min-width: $md) {
      margin-top: 115px;
    }

    & .container {
      width: 100%;
      max-width: 100%;
    }
  }

  &__ya {
    z-index: 2;
    position: relative;

    width: 100%;
    height: 100%;
    height: 500px;
    overflow: hidden;

    -ms-touch-action: auto !important;
    touch-action: auto !important;

    @media (min-width: $sm) {
      position: absolute;
      top: 0;
      left: 0;
      height: 610px;
    }
  }

  & .container {
    position: relative;
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;

    @media (min-width: $sm) {
      max-width: 1440px;
      min-height: 610px;
    }
  }

  &__info {
    position: relative;
    padding: 10% 5%;
    // padding-top: 650px;
    background-color: $white-color;
    width: 100%;

    border-radius: 10px;

    @media (min-width: $sm) {
      z-index: 3;
      position: absolute;
      padding: 10% 5%;
      bottom: auto;
      top: 10%;
      left: 0;
      max-width: 370px;
    }
    @media (min-width: $md) {
      padding: 5%;
      max-width: 610px;
      border-radius: 0;
    }

    &-wrapper {
      @media (min-width: $sm) {
        max-width: 282px;
        margin: 0 auto;
      }
    }

    &-title {
      margin-bottom: 24px;
      font-weight: 700;
      font-size: 18px;
      line-height: 140%;
      color: $primary-color;
      text-transform: uppercase;

      @include ff(
        'font-size',
        (
          320px: 18px,
          768px: 20px,
        )
      );

      @media (min-width: $xs) {
        margin-bottom: 38px;
      }
    }

    & ul {
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: $dark-color;

      @include ff(
        'font-size',
        (
          320px: 16px,
          768px: 28px,
        )
      );
    }

    & li {
      margin-bottom: 7px;

      @media (min-width: $xs) {
        margin-bottom: 15px;
      }
    }
  }
}
