:root {
  --bg-h: 17vw;

  @media (min-width: $xl) {
    --bg-h: 235px;
  }
}

.promo {
  position: relative;
  max-width: 1440px;
  margin: 0 auto;
  padding-bottom: 60px;

  display: flex;
  flex-direction: column;
  align-items: center;

  text-align: center;
  color: $dark-color;
  overflow: hidden;
  background-color: $bg-color;

  &::before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    padding-top: var(--bg-h);

    background: url('../images/promo/1/1.png') 0 0 no-repeat,
      url('../images/promo/1/2.png') 50% 0 no-repeat,
      url('../images/promo/1/3.png') 100% 0 no-repeat;

    background-size: 36% var(--bg-h), 40% var(--bg-h), 30% var(--bg-h);
    background-position: 0 0, 55% 0, 100% 0;

    // @media (min-width: $xs) {
    //   padding-top: var(--bg-h);
    //   background: url('../images/promo/1/1.png') 0 0 no-repeat,
    //     url('../images/promo/1/2.png') 50.5% 0 no-repeat,
    //     url('../images/promo/1/3.png') 100% 0 no-repeat;
    //   background-size: 36% var(--bg-h),40% var(--bg-h), 30% var(--bg-h);
    //   background-position: 0 0,55% 0,100% 0;
    // }

    // @media (min-width: $sm) {
    //   padding-top: var(--bg-h);
    //   background: url('../images/promo/1/1.png') 0 0 no-repeat,
    //     url('../images/promo/1/2.png') 50.25% 0 no-repeat,
    //     url('../images/promo/1/3.png') 100% 0 no-repeat;
    //     background-size: 36% var(--bg-h),40% var(--bg-h), 30% var(--bg-h);
    //     background-position: 0 0,55% 0,100% 0;
    // }

    @media (min-width: $xxl) {
      padding-top: calc(var(--bg-h) + 120px);
      // background-size: 36% var(--bg-h),40% var(--bg-h), 30% var(--bg-h);
      // background-position: 0 0,55% 0,100% 0;
    }
  }

  & .container {
    max-width: 1110px;

    padding: 0 15px;
    padding-top: calc(var(--bg-h) + 24px);

    @media (min-width: $sm) {
      padding-top: calc(var(--bg-h) + 60px);
    }
  }

  &__title {
    max-width: 100%;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 33px;
    line-height: 110%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 33px,
        620px: 43px,
        768px: 67px,
        1200px: 88px,
      )
    );
  }

  &__price {
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin-bottom: 38px;
    font-weight: 400;
    font-size: 22px;
    line-height: 100%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 22px,
        768px: 36px,
        1200px: 52px,
      )
    );

    & span {
      @include ff(
        'font-size',
        (
          320px: 16px,
          768px: 26px,
          1200px: 32px,
        )
      );
    }
  }

  &__text {
    max-width: 100%;
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 19px;
    line-height: 140%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 19px,
        768px: 22px,
        1200px: 24px,
      )
    );
  }
}
