.gallery {
  padding-top: 100px;
  @media (min-width: $sm) {
    padding-top: 124px;
  }
  @media (min-width: $xl) {
    padding-top: 115px;
  }

  &.container {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
  }

  &__item {
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
    margin-bottom: 54px;
    @media (min-width: $md) {
      margin-bottom: 115px;
    }

    & picture,
    & img {
      width: 100%;
      height: 100%;
      min-height: 245px;
    }
  }

  &__title {
    display: none;
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 18px;
    line-height: 140%;
    text-transform: uppercase;

    @media (min-width: $sm) {
      margin-bottom: 44px;
      font-size: 20px;
    }
    @media (min-width: $md) {
      display: block;
    }
    @media (min-width: $xl) {
      margin-bottom: 33px;
    }

    &--mob {
      display: block;
      @media (min-width: $md) {
        display: none;
      }
    }
    & span {
      color: $primary-color;
    }
  }

  &__scroll {
    display: flex;
    display: grid;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    column-gap: 10px;
    padding-left: 15px;

    @media (min-width: $md) {
      display: grid;
      padding-left: 0;
    }
  }

  &__title {
    width: 100%;
    margin-bottom: 30px;

    @media (max-width: $sm) {
      max-width: 640px;
    }
  }

  &__pic {
    display: flex;
    overflow: hidden;
    border-radius: 10px;

    width: 262px;
    height: 190px;

    @media (min-width: $sm) {
      width: 524px;
      height: 368px;
    }
    @media (min-width: $md) {
      width: auto;
      height: auto;
    }

    & picture {
      @media (min-width: $sm) {
        min-height: 245px;
      }

      & img {
        height: 100%;
        @media (min-width: $sm) {
          min-height: 245px;
        }
      }
    }
  }

  &__item--pool {
    & .gallery__scroll {
      @media (min-width: $md) {
        grid-template-columns: minmax(400px, 2fr) 1fr 2fr;
      }
      @media (min-width: $xl) {
        grid-template-columns: minmax(580px, 2fr) 1fr 2fr;
      }
    }

    & .gallery__title {
      @media (min-width: $md) {
        grid-column-start: 2;
        grid-column-end: 4;
        grid-row-start: 1;
        grid-row-end: 2;
        padding-right: 20vw;
        margin-top: auto;
        margin-bottom: -30px;
        align-self: flex-end;
      }
    }

    & .gallery__pic {
      @media (min-width: $sm) {
        min-height: 245px;
      }
    }

    & .gallery__pic--left {
      @media (min-width: $md) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 3;
        min-height: 400px;
      }

      & picture {
        @media (min-width: $md) {
          min-height: 400px;
        }

        & img {
          height: 100%;
          @media (min-width: $md) {
            min-height: 400px;
          }
        }
      }
    }

    & .gallery__pic--right {
      @media (min-width: $md) {
        grid-column-start: 3;
        grid-column-end: 4;
        grid-row-start: 2;
        grid-row-end: 3;
        height: 245px;
      }
    }

    & .gallery__pic--medium {
      @media (min-width: $md) {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
      }
    }
  }

  &__item--gym,
  &__item--lessons {
    @media (min-width: $md) {
      background-color: $white-color;
    }

    & .gallery__scroll {
      @media (min-width: $md) {
        grid-template-columns: 2fr minmax(300px, 1fr) minmax(400px, 2fr);
      }
      @media (min-width: $xl) {
        grid-template-columns: 2fr 1fr minmax(580px, 2fr);
      }
    }

    & .gallery__title {
      @media (min-width: $md) {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 2;
        padding-left: 20vw;
        padding-right: 0;
        margin-top: auto;
        margin-left: auto;
        margin-bottom: -30px;
        align-self: flex-end;
      }
    }

    & .gallery__pic {
      align-items: flex-end;
      @media (min-width: $sm) {
        min-height: 245px;
      }
    }

    & .gallery__pic--left {
      @media (min-width: $md) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 3;
        height: 245px;
      }
    }

    & .gallery__pic--right {
      @media (min-width: $md) {
        grid-column-start: 3;
        grid-column-end: 4;
        grid-row-start: 1;
        grid-row-end: 3;
        min-height: 400px;
      }

      & picture {
        display: flex;
        min-height: 400px;
      }

      & img {
        height: 100%;
        min-height: 400px;
      }
    }

    & .gallery__pic--medium {
      @media (min-width: $md) {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
      }
    }
  }

  &__item--spa {
    & .gallery__scroll {
      @media (min-width: $md) {
        grid-template-columns: 2fr 1fr 2fr;
        align-items: flex-start;
        gap: 13px;
      }
    }

    & .gallery__title {
      @media (min-width: $md) {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 3;
        grid-row-end: 4;
      }
    }

    & .gallery__pic {
      &--one {
        @media (min-width: $md) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 1;
          grid-row-end: 4;
        }
      }

      &--two {
        display: none;
        @media (min-width: $md) {
          display: grid;
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 4;
          grid-row-end: 5;

          & img,
          & picture {
            display: flex;

            margin-left: auto;
            width: auto;
            min-height: auto;
          }
        }
      }

      &--three {
        @media (min-width: $md) {
          grid-column-start: 2;
          grid-column-end: 3;
          grid-row-start: 1;
          grid-row-end: 3;
        }
      }

      &--four {
        @media (min-width: $md) {
          grid-column-start: 2;
          grid-column-end: 3;
          grid-row-start: 4;
          grid-row-end: 5;
        }
      }

      &--five {
        @media (min-width: $md) {
          grid-column-start: 3;
          grid-column-end: 4;
          grid-row-start: 2;
          grid-row-end: 5;

          & img,
          & picture {
            display: block;
          }
        }
      }
    }
  }
}

.mCustomScrollbar {
  &.scroll-gallery {
    & > div > div {
      display: grid;
      gap: 10px;
      grid-auto-flow: column; // без переноса на новыю строку
    }
  }
}

.scroll-gallery {
  display: grid;
  gap: 10px;
  grid-auto-flow: column; // без переноса на новыю строку
  overflow-x: auto;
}
