.advantages {
  padding-top: 100px;
  padding-bottom: 54px;
  background-color: $white-color;

  @media (min-width: $sm) {
    padding-top: 70px;
  }
  @media (min-width: $md) {
    background-color: transparent;
    padding-top: 0;
  }


  & .element-animation.element-show {
    transition-duration: 0.5s;
  }

  & .container {
    width: 100%;
    max-width: 1110px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
    width: 100%;
    // padding-top: 44px;
    padding-bottom: 44px;
    margin-bottom: 34px;
    transition: 0.3s;

    @media (min-width: 880px) {
      flex-direction: row;
      align-items: stretch;
    }
  }

  &__item {
    position: relative;
    width: 100%;
    max-width: 300px;
    padding-top: 40px;
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 0.3s;

    @media (min-width: 375px) {
      padding-top: 45px;
      padding-bottom: 45px;
    }
    @media (min-width: 690px) {
      padding-top: 30px;
      padding-bottom: 30px;
      min-height: 295px;
    }

    @media (min-width: $sm) {
      padding-top: 50px;
      padding-bottom: 50px;
      max-width: 407px;
      min-height: auto;
    }
    @media (min-width: 880px) {
      width: calc(100% / 3 - 20px);
    }
    @media (min-width: $xl) {
      padding-top: 70px;
      padding-bottom: 70px;
    }
    // @media (min-width: $xxl) {
    //   padding-top: 100px;
    //   padding-bottom: 100px;
    // }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      height: 120%;
      min-height: 296px;
      display: block;
      transform: translate(-50%, -50%);
      border-radius: 50%;
      border: 1px solid;

      @media (min-width: $xs) {
        width: 120%;
        height: 120%;
      }
      @media (min-width: $sm) {
        width: 100%;
        height: 120%;
      }
      @media (min-width: 880px) {
        width: 120%;
        height: 100%;
      }
    }
  }

  &__item-inner {
    position: relative;
    padding-top: 60px;
    text-align: center;
    width: 260px;
    transition: 0.3s;

    @media (min-width: 375px) {
      width: 270px;
    }
    @media (min-width: $xs) {
      width: 275px;
      padding-top: 100px;
    }
    @media (min-width: $sm) {
      padding-top: 120px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 50%;
      height: 50px;
      width: 50px;
      transform: translateX(50%);
      background-image: var(--pic);
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: contain;

      @media (min-width: $xs) {
        height: 80px;
        width: 80px;
      }
      @media (min-width: $sm) {
        height: 90px;
        width: 90px;
      }
    }
  }

  &__item-title {
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    text-transform: uppercase;
    color: $primary-color;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 20px,
        870px: 16px,
        1200px: 20px,
      )
    );

  }

  &__item-text {
    max-width: 230px;
    margin: 0 auto;
    font-weight: 400;
    font-size: 16px;
    color: $dark-color;

    @include ff(
      'font-size',
      (
        320px: 16px,
        500px: 14px,
        768px: 20px,
        870px: 14px,
        1200px: 20px,
      )
    );

    @media (min-width: $sm) {
      max-width: 270px;
    }

  }

  &__info {
    display: inline-flex;
    flex-wrap: wrap;
    padding-left: 22px;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;

    @include ff(
      'font-size',
      (
        320px: 16px,
        768px: 20px,
      )
    );
  }

  &__blockquote {
    display: inline-flex;
    position: relative;
    font-weight: 700;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -22px;
      width: 4px;
      height: 100%;
      background-color: $dark-color;
    }
  }

  &__button-pay {
    display: block;
    margin: 0 auto;
  }
}
