.faq {
  padding-top: calc(115px - 27px);
  padding-bottom: calc(115px - 27px);

  width: 100%;
  max-width: 1110px;
  padding: 0 15px;
  margin: 100px auto;

  @media (min-width: $sm) {
    margin-top: 124px;
    margin-bottom: 124px;
  }
  @media (min-width: $xxl) {
    margin-top: 115px;
    margin-bottom: 115px;
  }

  &__list {
    margin-top: 27px;
    // margin-bottom: 27px;
    width: 100%;
    border-top: 1px solid $border-color;

    font-weight: 400;
    font-size: 15px;
    line-height: 130%;

    @include ff(
      'font-size',
      (
        320px: 16px,
        670px: 20px,
        1280px: 15px,
      )
    );
  }
}
