// Глобальные переменные

$mainFont: 'Gotham Pro', Arial, Helvetica, sans-serif;

// Size
$mainFontSize: 16px;
$mainLineHeight: 130%;
$boldFontWeight: 700;
$mediumFontWeight: 400;

// colors
$white-color: #ffffff;
$dark-color: #171818;
$contrast-color: #f5f5f5;
$primary-color: #e46821;
$primary-hover-color: #c95a1c;

$blue-color: #204860;
$yellow-color: #f3d27e;
$gray-color: #8e8e8e;
$bg-color: #f3f6f8;
$border-color: #171818;

$transition-base: 0.3s;

$borderRadius: 16px; // 16px
$borderRadius22: 22px; // 16px
$boxShadow: 8px 8px 16px rgba(73, 76, 79, 0.19), -8px -8px 10px #ffffff;
$boxShadowdark: 4px 4px 8px rgba(0, 0, 0, 0.25);
$boxShadowdarkInner: inset 0 -3em 1em rgba(0, 0, 0, 0.1),
  0 0 0 2px rgb(255, 255, 255), 0.3em 0.3em 1em rgba(0, 0, 0, 0.3);

$w-size: 1440px;

$xxxs: 420px;
$xxs: 480px;
$xs: 576px;
$xsm: 767px;
$sm: 768px;
$md: 992px;
$xl: 1200px;
$lg: 1300px;
$xxl: 1440px;
$xlg: 1600px;
$xxlg: 1920px;

:root {
  --bs-font-sans-serif: 'Gotha Pro', Arial, Helvetica, sans-serif;
}

$tooltip-bg: red;
