.profit {
  background-color: $bg-color;
  & .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1440px;

    padding-top: 75px;
    padding-bottom: 54px;

    background-color: $blue-color;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-image: url('../images/profit/bg-1.webp');

    @media (min-width: $xs) {
      padding-top: 54px;
    }
    @media (min-width: $sm) {
      background-image: url('../images/profit/bg-1t.webp');
    }
    @media (min-width: $md) {
      background-image: url('../images/profit/bg-1d.webp');
    }

    @media (max-width: $sm) {
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: 50px;

    @media (min-width: $sm) {
      margin-bottom: 60px;
    }

    @media (min-width: $md) {
      margin-bottom: 50px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;

    @media (min-width: $xs) {
      flex-direction: row;
      flex-wrap: wrap;
    }
    @media (min-width: $md) {
      gap: 60px;
    }
  }

  &__item {
    margin-bottom: 60px;
    text-align: center;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    &:last-child {
      margin-bottom: 0;
    }

    @media (max-width: $sm) {
      &:first-child .profit__item-inner {
        padding-top: 90px;
      }
    }

    @media (min-width: $xs) {
      width: calc(100% / 2 - 50px);
      margin-bottom: 44px;

      &:first-child {
        width: 100%;
      }
    }

    @media (min-width: $md) {
      width: calc(100% / 3 - 45px);

      &:first-child,
      &:nth-child(2) {
        width: calc(100% / 2 - 50px);
      }

      &:first-child {
        & .profit__item-inner {
          margin-left: auto;

          &::before {
            height: 80px;
          }
        }
      }
      &:nth-child(2) {
        & .profit__item-inner {
          margin-right: auto;
        }
      }
    }

    @media (min-width: $xl) {
      width: calc(100% / 3 - 80px);
    }
  }

  &__item-inner {
    position: relative;
    width: 100%;
    max-width: 247px;
    padding-top: calc(80px + 30px);

    @media (min-width: $sm) {
      max-width: 375px;
    }
    @media (min-width: $md) {
      max-width: 400px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 50%;
      height: 90px;
      width: 90px;
      transform: translateX(50%);
      background-image: var(--pic);
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: contain;
    }
  }

  &__item-title {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: $yellow-color;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 20px,
      )
    );
  }

  &__item-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: $white-color;

    @include ff(
      'font-size',
      (
        320px: 16px,
        768px: 20px,
      )
    );
  }
}
